
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
XInput,
XButton,
Group,
Cell,
Countdown
},
data () {
return {
valid1: false,
valid2: false,

vifphone:'',
vifemail:'',
submit001:'确定',

timeshow:false,
time:15,
start:false,
srartbtn:true,

hasphone:true,
hasemail:true,

pass:null,
}
},
mounted: function() {
this.pass={
phone:true,
email:true,
}
},
methods: {

submit () {
if (this.hasphone && this.pass.phone){
var phonereg = /^1[3|4|5|7|8][0-9]{9}$/; 
alert(phonereg.test(this.vifphone));
}
if (this.hasemail && this.pass.email){
var emailreg = /^(\w)+(\.\w+)*@(\w)+((\.\w{2,3}){1,3})$/; 
alert(emailreg.test(this.vifemail));
}
},
startCountdown () {
this.timeshow=true;
this.start=true;
this.srartbtn=false
},
finish (index) {
this.timeshow = false;
this.time = 15;
this.srartbtn=true;
this.start=false;
},
onFocus () {
}
}
}



