import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex);

const state = {
    'goods': null,

};
//  mutation 函数
const mutations = {
    goodsupdate (state,goods) {
      // 变更商品类型状态
      state.goods=goods
      console.log(state.goods)
    },
};

//export default new Vuex.Store({
//    state,
//    mutations
//})
export default new Vuex.Store({
    state,
    mutations,

});



