
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'app',
directives: {
TransferDom
},
components: {
XHeader,
Tabbar,
TabbarItem,
Group,
Cell
},
data () {
return {
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
showMenus: true,
footerbarshow: true
}
},
watch:{
"$route": "footerbar"
},
computed: {

},
mounted () {
this.$root.eventHub.$on('hidebar', (data)=>{
this.footerbarshow=data
})
},

methods: {
menu(){
this.$router.push({path: '/my'})
},
footerbar(){
if(this.$route.name=='details'||this.$route.name=='pay'){
this.footerbarshow=false
}else {
this.footerbarshow=true
}

},
}
}



