
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'



export default {
components: {
Swiper,
Masker,
Search,
Spinner
},
data () {
return {
msg: 'Hello World!',
page:0,
scrollpage:true,
crescent1:'crescent',
spinner:false,
scrollfirst:true,

demo01_list: [{
id:'idkd',
url: 'http://www.baidu.com',
img: 'https://static.vux.li/demo/1.jpg',
title: '送你一朵fua'
}, {
id:'idmkd',
url: 'http://localhost:8080/#/my',
img: 'https://static.vux.li/demo/2.jpg',
title: '送你一辆车'
}, {
id:'jkdkdsss',
url: 'javascript:',
img: 'https://static.vux.li/demo/3.jpg',
title: '送你一次旅行'
}],

list: [],
results: [],
value: ''
}
},
created :function(){
},
mounted () {









if(this.scrollpage){
window.addEventListener('scroll', this.handleScroll);
}

Date.prototype.format = function(format) {
var o = {
"M+": this.getMonth() + 1, //month
"d+": this.getDate(), //day
"h+": this.getHours(), //hour
"m+": this.getMinutes(), //minute
"s+": this.getSeconds(), //second
"q+": Math.floor((this.getMonth() + 3) / 3), //quarter
"S": this.getMilliseconds() //millisecond
}
if (/(y+)/.test(format)) {
format = format.replace(RegExp.$1,(this.getFullYear() + "").substr(4 - RegExp.$1.length));
}
for (var k in o){
if (new RegExp("(" + k + ")").test(format)){
format = format.replace(RegExp.$1,RegExp.$1.length == 1 ? o[k] :("00" + o[k]).substr(("" + o[k]).length));
}
}
return format;
}
},
methods: {
handleScroll () {
let scrollBottom=document.body.clientHeight-window.innerHeight -document.body.scrollTop
if(scrollBottom<10&&this.scrollpage){

if(this.scrollfirst){
this.scrollfirst=false;
this.spinner=true;
let pagenum=this.page;
pagenum++;
this.$http.get('/api/articleList/'+pagenum)
.then((response) => {
this.page=pagenum
let arrcon=this.list
let arrnew=response.data
this.list=arrcon.concat(arrnew);
this.spinner=false;
this.scrollpage=true;
this.scrollfirst=true;
if(response.data.length<10){
this.scrollpage=false;
return false
}
})
.catch(function (error) {
});

}


console.log(this.scrollpage)
console.log(scrollBottom)






}
},
details (id) {
this.$router.push({ name: 'details', params: { id: id }})
},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult () {

},
onSubmit () {
},
onFocus () {
},
onCancel () {
}
},
}



